* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #1c1c1c;
  font-family: 'Roboto', sans-serif;
  color: #f0f0f0;
  min-height: 100vh;
}

.app {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 50px;
  color: #ffffff;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  background: linear-gradient(145deg, #292929, #161616);
  border-radius: 20px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
}

h1, h2, h3 {
  font-family: 'Orbitron', sans-serif;
  margin: 10px 0;
  text-shadow: 0 0 10px rgba(0, 255, 255, 0.7);
}

.song-structure {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  gap: 20px;
}

.main-content {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  width: 100%;
}

.tracks-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  flex: 2;
}

.loop-selector-container {
  width: 25%;
  padding: 20px;
  border-left: 2px solid #444;
  background-color: #252525;
  box-sizing: border-box;
  border-radius: 10px;
}

.track {
  width: 100%;
  padding: 15px;
  margin-bottom: 20px;
  background-color: #2c2c2c;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #f0f0f0;
  border-radius: 8px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.track-header {
  font-weight: bold;
  font-size: 20px;
  margin-right: 10px;
}

.track-body {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.loop-area {
  width: 200px;
  height: 40px;
  border: 1px dashed #0ff;
  background-color: rgba(0, 255, 255, 0.1);
  margin-top: 10px;
  color: #0ff;
  box-sizing: border-box;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}

.loop-area:hover {
  background-color: rgba(0, 255, 255, 0.2);
}

.loop-selector {
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: center;
}

button {
  padding: 12px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 8px;
  min-width: 140px;
  position: relative;
  z-index: 1;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s ease, transform 0.3s ease;
}

button:hover {
  background-color: #45a049;
  transform: scale(1.05);
}

.selection-summary-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  width: 100%;
  margin-top: 20px;
}

.selection-summary {
  flex: 1;
  padding: 15px;
  border: 1px solid #444;
  background-color: #1f1f1f;
  text-align: left;
  color: #f0f0f0;
  min-width: 250px;
  border-radius: 8px;
}

.selection-summary h2 {
  text-align: center;
  margin-bottom: 15px;
  font-size: 22px;
}

.selection-summary ul {
  list-style-type: none;
  padding: 0;
}

.selection-summary li {
  padding: 5px 0;
  font-size: 16px;
  border-bottom: 1px solid #333;
}

.loop-options {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
}

.loop-options button {
  padding: 12px 18px;
  font-size: 16px;
  border-radius: 8px;
  background-color: #333;
  color: #f0f0f0;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.loop-options button:hover {
  background-color: #444;
}

.loop-item {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.instrument-image {
  width: 90px;
  height: 90px;
  margin-right: 15px;
}

.instrument-name {
  font-size: 18px;
  color: #0ff;
  text-shadow: 0 0 10px rgba(0, 255, 255, 0.7);
}

.track {
  width: 100%;
  padding: 15px;
  margin-bottom: 20px;
  background: linear-gradient(135deg, rgba(0, 255, 128, 0.4), rgba(0, 32, 16, 0.1));
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #f0f0f0;
  border-radius: 8px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.buttons-container {
  display: flex;
  justify-content: space-between; /* Alinea los botones a la izquierda y a la derecha */
  width: 100%; /* Hace que los botones ocupen todo el ancho disponible */
  gap: 10px; /* Espacio entre los botones */
}

.continue-button, .play-button {
  padding: 40px 200px;
  font-size: 30px;
  cursor: pointer;
}

.continue-button.enabled {
  background-color: green;
  color: white;
}

.continue-button.disabled {
  background-color: gray;
  color: white;
}

.play-button.enabled {
  background-color: green;
  color: white;
}

.play-button.disabled {
  background-color: gray;
  color: white;
}


